.now{
  margin-top: 25px;

  &__block-btn{
    display: flex;
    justify-content: center;
    margin-top: 55px;
  }

  .genre{
    margin-top: 13px;
    display: flex;
    width: 765px;
    row-gap: 20px;
    column-gap: 40px;
  }

  .cinema{
    margin-top: 62px;
  }

  &__background{
    background-image: url(../img/А1.webp);
    background-repeat: no-repeat;
    width: 100%;
    max-width: 1920px;
    min-height: 1000px;
    position: absolute;
    top: -130px;
    margin: 0 auto;
    left: 0;
    right: 0;
    z-index: -1;
  }

  .cinema{
    &__item:last-child{
      display: none;
    }
  }
}

@media (max-width: 1400px){

  .now{
    margin-top: 17px;

    &__background{
      display: none;
    }

    &__menu{
      flex-direction: column;
      align-items: normal;
    }

    &__block-btn{
      margin-top: 37px;
    }

    .genre{
      margin-top: 3px;
      column-gap: 25px;
    }

    .cinema {
      margin-top: 30px;

      &__list{
        grid-auto-rows: minmax(275px, auto);
        row-gap: 33px;
      }
    }
  }
}

@media (max-width: 800px){

  .now{

    .genre{
      flex-wrap: wrap;
      width: 100%;
      column-gap: 40px;
    }

    &__block-btn{
      margin-top: 0;
    }

    .cinema{
      &__item:last-child{
        display: block;
      }
    }
  }
}


@media (max-width: 525px){

  .now{
    margin-top: 27px;

    &__t2{
      position: relative;

      &::after{
        content: url(../img/svg/bur.svg);
        top: -2px;
        right: -24px;
        position: absolute;
      }
    }

    &__menu{
      margin-top: 10px;
    }

    &__title{
      display: flex;
      column-gap: 10px;
      align-items: center;
      justify-content: center;
    }

    &__block-btn{
      margin-top: 10px;
    }

    .genre{
      display: none;
    }

    .cinema{
      margin-top: 22px;

      &__list{
        row-gap: 10px;
      }

      &__item:nth-last-child(-n + 3){
        display: none;
      }
    }
  }
}
